.navbar-area .logoName {
  font-size: 21px;
  font-weight: bold;
  display: flex;
  align-items: center; }

.navbar-area #selectedMenuStyle {
  color: #ABD037 !important;
  text-decoration: underline !important;
  font-weight: 800 !important;
  cursor: pointer; }

.navbar-area #unselectedMenuStyle {
  color: #08AB93 !important;
  cursor: pointer; }

.navbar-area #notificationIcon {
  font-size: 26px; }

.navbar-area #favoriteIcon {
  font-size: 26px; }

.navbar-area #icons {
  display: flex;
  justify-content: space-evenly; }

.navbar-area .dropdown {
  position: relative;
  display: inline-block;
  z-index: 1000; }

.navbar-area .dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1; }

.navbar-area .register-modal {
  display: flex; }

.navbar-area #switch-text {
  cursor: pointer; }

.navbar-area .dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block; }

.navbar-area #option-view {
  display: flex; }

.navbar-area .dropdown-content a:hover {
  background-color: #f1f1f1; }

.navbar-area .dropdown:hover .dropdown-content {
  display: block; }

.navbar-area .dropdown:hover .dropbtn {
  background-color: #3e8e41; }

.navbar-area #notificationBadge {
  background-color: #08AB93;
  font-size: 12px;
  position: relative;
  top: -10px;
  right: 10px;
  z-index: 1;
  height: 20px; }

.navbar-area ::webkit-appearance #notificationBadge {
  background-color: #08AB93;
  font-size: 12px;
  position: relative;
  top: -10px;
  right: 10px;
  z-index: 1;
  height: fit-content; }

.navbar-area .notificationContainer {
  margin-top: 15px;
  margin-left: 7px;
  flex-direction: row;
  justify-content: space-between; }

.navbar-area .favoriteContainer {
  margin-top: 15px;
  flex-direction: row;
  justify-content: space-between; }

.navbar-area .logout {
  cursor: pointer; }

.navbar-area #userName {
  margin-top: 12px;
  font-size: 15px;
  margin-left: 18px;
  font-weight: bold; }

@media (min-width: 768px) and (max-width: 992px) {
  .navbar-area #userName {
    position: relative !important;
    width: 65px !important;
    flex-wrap: wrap !important;
    display: flex !important;
    word-break: break-all !important; }
  .navbar-area .profile-image {
    margin-left: 0px !important; }
  .navbar-area #container-view {
    max-width: 90% !important;
    margin: 0px !important;
    padding: 0px !important; }
  .navbar-area .profile-image > div {
    align-items: center !important; }
  .navbar-area .navbar-area > div {
    padding-left: 8px; }
  .navbar-area .comero-nav .navbar .navbar-nav .nav-item.p-relative {
    position: relative !important;
    width: max-content !important; } }

.navbar-area .rounded-circle {
  margin-left: 12px;
  min-width: 48px; }

.navbar-area .username {
  display: flex;
  align-items: center;
  font-weight: bold;
  margin-left: 22px;
  cursor: pointer;
  font-weight: bold; }

.navbar-area .profile-image {
  width: 35%;
  margin-left: 22px; }

#dropdown-view {
  height: max-content; }

#menu-titles {
  display: flex;
  justify-content: center; }

#container-view {
  max-width: 97% !important; }

#icon-view {
  display: flex;
  align-content: center; }

@media (min-width: 1200px) {
  .container {
    max-width: 1267px !important; } }

#profile-align {
  flex-wrap: nowrap !important;
  display: flex;
  justify-content: center;
  align-items: center; }

@media screen and (max-width: 1329px) {
  .logo-header {
    margin-right: 3% !important; }
  .navbar-nav {
    width: 100%; }
  #unselectedMenuStyle {
    font-size: 12px !important; } }

@media (max-width: 864px) and (min-width: 576px) {
  #profile-view {
    display: flex !important;
    flex-direction: column !important; }
  .pointer {
    display: flex !important;
    cursor: pointer !important; } }
