.notification .notification {
  align-content: center; }

.notification .date {
  color: #08AB93;
  font-size: 15px; }

.notification .contentViewSeen {
  display: flex;
  justify-content: center;
  margin: 2%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  /* 5px rounded corners */ }

.notification .contentViewSent {
  margin: 2%;
  border: 5px solid #08AB93;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  /* 5px rounded corners */ }

.notification .ViewDetail {
  width: 140px; }

.notification .no_data_message {
  font-weight: bolder;
  color: #08AB93; }

.notification .no_data_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.notification #dismiss-details-button {
  width: 140px;
  margin-bottom: 10px; }

.notification #notification-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%; }

.notification #view-details-button {
  color: #fff; }

.notification .contentViewSent {
  display: flex;
  justify-content: center; }

.notification #view-details-button:hover {
  background-color: #fff !important;
  border: 1px solid #08AB93 !important;
  color: #08AB93 !important; }

.notification #dismiss-details-button:hover {
  background-color: #fff !important;
  border: 1px solid red !important;
  color: red !important; }

.notification #dismiss-all-button:hover {
  background-color: #fff !important;
  border: 1px solid red !important;
  color: red !important; }

.notification #notification-buttons {
  display: flex;
  align-items: center; }

.notification #date-format {
  display: flex;
  align-items: center; }

.notification #chef-info-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.notification .nodata-content {
  display: flex;
  flex-direction: column;
  align-items: center; }

.notification .notification-common-content {
  width: 95%; }

.notification .chefname {
  cursor: pointer;
  font-size: 18px;
  font-weight: bolder; }

.notification .notification-user-image {
  height: 150px;
  border-radius: 10px;
  margin: 10px; }

.notification #image-view {
  display: flex;
  justify-content: center; }

.notification #Viewdetails-button {
  margin-left: 10%;
  min-width: 135px;
  display: flex;
  align-items: center; }

.notification #page-full-content {
  display: flex;
  justify-content: center; }

.notification #chef-detail-content {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.notification #date-content {
  display: flex;
  align-items: center; }

.notification #notification-button-style {
  display: flex;
  align-items: center; }

.notification #favorite-list {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.notification .load-more-button {
  display: flex;
  justify-content: center; }

.notification #notification-status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.notification #date-format {
  display: flex;
  align-items: center; }

@media (min-width: 300px) and (max-width: 799px) {
  .notification .products-image {
    display: flex;
    justify-content: center;
    align-items: center; }
  .notification .notification-user-image {
    max-width: 65% !important; }
  .notification #chef-info-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .notification #date-format {
    display: flex;
    align-items: center;
    justify-content: center; }
  .notification #notification-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3%;
    margin-bottom: 5%; } }

@media (max-width: 991px) {
  #notification-button-style {
    display: flex;
    justify-content: center; }
  #favorite-list {
    align-items: center; }
  .products-image {
    display: flex;
    justify-content: center; } }

@media (max-width: 499px) {
  #dismiss-all-button {
    margin-bottom: 2%; } }

.load-more-button {
  display: flex;
  justify-content: center; }

@media (max-width: 800px) {
  .ViewDetail {
    width: 140px; } }

.notification-containar {
  box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08); }

.notification > div {
  padding-bottom: 2%; }

@media (max-width: 767px) {
  .contentViewSent {
    margin-bottom: 10% !important; } }
