.payment {
  /* On mouse-over, add a deeper shadow */ }
  .payment .payment {
    align-content: center; }
  .payment .date {
    color: brown;
    font-size: 15px; }
  .payment .contentView {
    display: flex;
    justify-content: center;
    margin: 2%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 5px;
    /* 5px rounded corners */ }
  .payment .contentView:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }
  .payment .nodata-content {
    color: #08AB93;
    font-weight: bolder; }
  .payment .ViewDetail {
    width: 120px; }
  .payment #payment-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .payment #view-details-button {
    color: #fff; }
  .payment #view-details-button:hover {
    background-color: #fff !important;
    border: 1px solid #08AB93 !important;
    color: #08AB93 !important; }
  .payment #date-format {
    display: flex;
    align-items: center; }
  .payment #chef-info-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .payment .nodata-content {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .payment .payment-common-content {
    width: 95%; }
  .payment .chefname {
    cursor: pointer;
    font-size: 18px;
    font-weight: bolder;
    margin-bottom: 15px !important; }
  .payment .payment-user-image {
    height: 125px;
    border-radius: 10px;
    margin: 10px; }
  .payment #image-view {
    display: flex;
    justify-content: center; }
  .payment #Viewdetails-button {
    margin-left: 10%;
    min-width: 135px;
    display: flex;
    align-items: center; }
  .payment #page-full-content {
    display: flex;
    justify-content: center; }
  .payment #chef-detail-content {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .payment #date-content {
    display: flex;
    align-items: center; }
  .payment #payment-button-style {
    display: flex;
    align-items: center; }
  .payment .load-more-button {
    display: flex;
    justify-content: center; }
  .payment #payment-status {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .payment #date-format {
    display: flex;
    align-items: center; }
  @media (min-width: 300px) and (max-width: 799px) {
    .payment .products-image {
      display: flex;
      justify-content: center;
      align-items: center; }
    .payment .payment-user-image {
      max-width: 65% !important; }
    .payment #chef-info-list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
    .payment #date-format {
      display: flex;
      align-items: center;
      justify-content: center; }
    .payment #payment-buttons {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 3%;
      margin-bottom: 5%; } }

@media (max-width: 991px) {
  #payment-button-style {
    display: flex;
    justify-content: center; }
  .products-image {
    display: flex;
    justify-content: center; } }

@media (max-width: 767px) {
  .booking-date-view {
    display: flex;
    justify-content: center; } }

.loadmore-view {
  display: flex;
  justify-content: center;
  padding-bottom: 3%; }

.payment-containar {
  box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08); }

@media (max-width: 767px) {
  .contentView {
    padding-bottom: 5%; } }

.booking-date-view {
  display: flex;
  justify-content: center;
  margin-bottom: 15px !important; }

.payment-text {
  text-align: center; }
