.pricingModal input,
.pricingModal textarea {
  border: 1px solid #eeeeee;
  box-sizing: border-box;
  margin: 0;
  outline: none;
  padding: 10px; }

.pricingModal input[type="button"] {
  -webkit-appearance: button;
  cursor: pointer; }

.pricingModal input::-webkit-outer-spin-button,
.pricingModal input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.pricingModal .input-group {
  clear: both;
  margin: 15px 0;
  position: relative; }

.pricingModal .input-group input[type='button'] {
  background-color: #eeeeee;
  min-width: 38px;
  width: auto;
  transition: all 300ms ease; }

.pricingModal .input-group .button-minus,
.pricingModal .input-group .button-plus {
  font-weight: bold;
  height: 38px;
  padding: 0;
  width: 38px;
  position: relative; }

.pricingModal .input-group .quantity-field {
  position: relative;
  height: 38px;
  left: -6px;
  text-align: center;
  width: 62px;
  display: inline-block;
  font-size: 13px;
  margin: 0 0 5px;
  resize: vertical; }

.pricingModal .button-plus {
  left: -13px; }

.pricingModal input[type="number"] {
  -moz-appearance: textfield;
  -webkit-appearance: none; }
