.settings {
  /* Rounded sliders */ }
  .settings .cart-totalsSettings {
    background: #ffffff;
    padding: 40px;
    max-width: 600px;
    -webkit-box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08);
    box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08);
    border-radius: 5px;
    margin: 45px auto 0; }
  .settings .cart-totalsSettings h3 {
    margin-bottom: 20px;
    font-size: 19px; }
  .settings .cart-totalsSettings ul {
    padding: 0;
    margin: 0 0 25px;
    list-style-type: none; }
  .settings .cart-totalsSettings ul li {
    border: 1px solid #eaedff;
    padding: 10px 15px;
    color: #222222;
    font-weight: 600;
    overflow: hidden; }
  .settings .cart-totalsSettings ul li:first-child {
    border-bottom: none; }
  .settings .cart-totalsSettings ul li:last-child {
    border-top: none; }
  .settings .cart-totalsSettings ul li span {
    float: right;
    color: #666666;
    font-weight: normal; }
  .settings .cart-totalsSettings ul li span b {
    font-weight: 600; }
  .settings .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px; }
  .settings .switch input {
    opacity: 0;
    width: 0;
    height: 0; }
  .settings .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s; }
  .settings .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s; }
  .settings input:checked + .slider {
    background-color: #08AB93; }
  .settings input:focus + .slider {
    box-shadow: 0 0 1px #2196F3; }
  .settings input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px); }
  .settings .slider.round {
    border-radius: 34px; }
  .settings .slider.round:before {
    border-radius: 50%; }
  .settings .listItem {
    height: 54px;
    display: flex;
    align-items: center;
    cursor: pointer; }
