#register-content, #login-content {
  box-shadow: 0 2px 48px 0 rgba(0, 0, 0, 0.08);
  padding: 20px; }

.auth .register_type {
  display: flex;
  justify-content: center; }
  .auth .register_type .chef_register_type {
    display: flex;
    justify-content: flex-end; }
    .auth .register_type .chef_register_type .chef_register_type_card {
      background: #ABD037;
      padding: 20px;
      cursor: pointer; }
      .auth .register_type .chef_register_type .chef_register_type_card .chef_register_type_card_div {
        text-align: center; }
        .auth .register_type .chef_register_type .chef_register_type_card .chef_register_type_card_div .chef_register_type_card_div_name {
          color: white; }
  .auth .register_type .customer_register_type {
    display: flex;
    justify-content: flex-end; }
    .auth .register_type .customer_register_type .customer_register_type_card {
      background: #08AB93;
      padding: 20px;
      cursor: pointer; }
      .auth .register_type .customer_register_type .customer_register_type_card .customer_register_type_card_div {
        text-align: center; }
        .auth .register_type .customer_register_type .customer_register_type_card .customer_register_type_card_div .customer_register_type_card_div_name {
          color: white; }

.auth .login-form .eyeIconView {
  display: flex; }

.auth .login-form i {
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  right: 45px;
  margin-top: 13px; }

.auth .login-form .passwordContainer {
  display: flex; }

.auth .login-form .changePwdInputLable {
  margin-bottom: 15px; }

.auth .login-form .passwordContainer .form-control {
  margin-bottom: 15px; }

.auth .login-form #resetPwdButton {
  margin-top: 50px; }

.auth .login-form .forgot-password {
  margin-top: 0px !important; }

.auth #socialLoginContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }
  .auth #socialLoginContainer #orFont {
    font-size: 20px; }

.auth .orFont {
  font-size: 20px; }

.auth .login-keep {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 15px; }

#register-content {
  width: 50%; }

#login-content {
  width: 50%; }

#google-id {
  margin-left: 15px; }

#login-submit-button {
  width: 50%; }

.login-btn-view {
  display: flex;
  justify-content: center; }

@media (min-width: 451px) and (max-width: 800px) {
  #register-content {
    width: 80%;
    display: flex;
    justify-content: center; }
  #login-content {
    width: 80%;
    display: flex;
    justify-content: center; } }

@media (min-width: 301px) and (max-width: 450px) {
  #register-content {
    width: 100%; }
  #login-content {
    width: 100%; } }

@media (min-width: 380px) and (max-width: 416px) {
  #keep-me-login {
    font-size: 12px; }
  #password-forgot-content {
    font-size: 12px; } }

@media (max-width: 767px) {
  #google-id {
    margin-left: 0px !important;
    width: 100% !important;
    margin-right: 7px !important; }
  .login-keep {
    display: flex;
    flex-direction: column;
    align-items: center; }
  #login-submit-button {
    width: 100% !important; }
  #password-forgot-content {
    width: 100% !important;
    top: 0px !important;
    text-align: center !important; }
  .terms {
    display: contents !important; } }
