.homePage .main-banner.item-bg1 {
  background-image: url(../../images/mock-image/main-banner2.jpg); }

.homePage .main-banner.item-bg2 {
  background-image: url(../../images/mock-image/main-banner1.jpg); }

.homePage .main-banner.item-bg3 {
  background-image: url(../../images/mock-image/main-banner3.jpg); }

.homePage .main-banner.item-bg4 {
  background-image: url(../../images/mock-image/main-banner2.jpg); }

.homePage .location {
  font-size: 35px;
  margin-left: 16px; }

.locationIconView {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: #f5f5f5;
  height: 45px;
  width: 8%; }

#current-locaton-view {
  margin-right: 8px;
  color: #08AB93; }

.location-text {
  font-size: 58px;
  font-weight: bolder;
  margin-bottom: 10px !important; }

.location-details-model {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 5%; }

@media (max-width: 991px) {
  .location-text {
    font-size: 28px;
    font-weight: bolder;
    margin-bottom: 10px !important; } }

#home-screen-view {
  overflow: hidden; }

.overview {
  background-color: white;
  padding: 50px;
  margin: 20px;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px; }

.statusMsg {
  font-size: larger;
  margin-bottom: 1%;
  display: flex; }

.verification {
  font-size: larger;
  color: red;
  margin-top: 11px;
  font-weight: bolder; }

.verificationSuccess {
  font-size: larger;
  color: #08AB93;
  margin-top: 11px;
  font-weight: bolder; }

.blocks {
  display: flex;
  justify-content: center;
  align-items: center; }

.response-view {
  font-weight: bolder;
  color: #08AB93; }

.tickIcon {
  font-size: 23px;
  padding-left: 10px;
  color: green; }

.crossIcon {
  font-size: 23px;
  padding-left: 10px;
  color: red; }

.h4 {
  color: #08AB93; }

.request {
  border: 1px solid #08AB93 !important;
  margin-bottom: 1px; }

.profile-pic {
  border-radius: 50%;
  width: 58px;
  height: 70px; }

.request-name {
  margin-top: 15px;
  margin-left: 7px; }

.button {
  height: 44px;
  margin-top: 30px;
  margin-left: 46px; }

.scroll {
  height: 347px;
  position: relative;
  /* overflow: scroll; */
  overflow-y: scroll; }

.bts-popup .bts-popup-container a {
  color: #000 !important; }

.bts-popup .bts-popup-container a:hover {
  color: #08AB93 !important; }

@media (max-width: 1075px) {
  #home-search-button {
    margin-right: 12px; } }

.homePage .main-banner.item-bg1 {
  height: calc(100vh - 68px); }
