.intro_container p.content-title {
  color: #08AB93 !important;
  font-size: 20px; }

.intro_container .slick-prev:before,
.intro_container .slick-next:before {
  color: #08AB93 !important; }

.intro_container .slick-prev:hover:before,
.intro_container .slick-prev:focus:before,
.intro_container .slick-next:hover:before,
.intro_container .slick-next:focus:before {
  opacity: 1; }

.intro_container .slick-dots li.slick-active button:before {
  color: #ABD037 !important; }
